.background-video{
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  z-index: -1;
}

.flex-container {
  display: flex;
  grid-gap: 10px;
  padding: 10px;
  flex-direction: row;
  height: 300px;
  color: white;

  &.flex-header{
    display: flex;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    
    img{
      border-radius: 1rem;
      opacity: 0.2;
    }
  }
}

.flex-item{
  width: 40%;
  height: 40%;
}